<template>
    <transition>
      <slot></slot>
    </transition>
</template>

<script>
    export default{
        name:'FadeAnimation',
        data(){
            return{

            }
        }
    }
</script>

<style rel="stylesheet/stylus" lang="stylus" scoped>
  .v-enter,.v-leave-to
    opacity:0
  .v-enter-active,.v-leave-active
    transition: opacity .5s
</style>



